body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: rgb(228, 228, 228); */
  background-image: linear-gradient(120deg, #DEDFE4,#8b8f9e);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.calc{
  width: fit-content;
  padding: 5px;
  background-color: rgb(255, 255, 255);
  background-image: linear-gradient(#41414B, #21212B);
  color: white;
  height: 400px;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.result{
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  background-color: rgba(128, 255, 0, 0);
  width: 80%;
  height: 100%;
  padding: 15px;
  font-size: 30px;
}
.btns{
  color: rgba(255, 255, 255, 0.623);
  font-size: 20px;
  display: grid;
  grid-template-columns: auto auto auto auto;
}
.btn{
  width: 50px;
  height: 50px;
  margin: 5px;
  background-image: linear-gradient(120deg, #18181b,#52586e);
  border-radius: 35%;
  box-shadow: inset 1px 1px 3px #afafaf, inset -1px -1px 3px rgb(26, 26, 26);
  
}
.secoundary{
  background-image: linear-gradient(120deg, #bd5500,#f5ad11);
  color: rgba(255, 255, 255, 0.692);
}
.primary{
  background-image: linear-gradient(120deg, #25282e,#676b7c);
}
.btn .in{
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: inherit;
  width: 100%;
  height: 100%;
  box-shadow: 5px 5px 10px #000000 ;
  user-select: none;
}
.zero{
  grid-column: 1 / span 2;
  width: 111px;
  border-radius: 18px;
}

.btnAnimate{
  width: 50px;
  height: 50px;
  margin: 5px;
  background-image: linear-gradient(120deg, #363b46,#898ea1);
  border-radius: 35%;
  box-shadow: inset 1px 1px 3px #afafaf;
  transition: all 2s linear;
}
.btnAnimate .in{
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: inherit;
  width: 100%;
  height: 100%;
  box-shadow: 5px 5px 10px #000000 ;
}
.btn .in:hover{
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .calc{
    width: 100%;
    height: 100%;
    border-radius: 0;
    display: grid;
    justify-content: center;
    grid-auto-rows: 20% 80%;
  }
  .result{
    
  }
  .btn{
    width: 75px;
    height: 75px;
    margin: 5px;
    font-size: 30px;
  }
  .zero{
    grid-column: 1 / span 2;
    width: 161px;
    border-radius: 28px;
  }
}